.eurotour-countries {
  @include Montserrat;
  @include font(16px, 28px);
  color: $light-text-color;
}

.eurotour-countries__headline {
  margin-bottom: 64px;
  letter-spacing: -1.245px;
}

.eurotour-countries__headline::after {
  bottom: -31px;
  width: 228.5px;
  height: 4px;
}

.eurotour-countries__header {
  padding-right: 800px;
  margin-bottom: 46px;
}

.eurotour-countries__description {
  margin: 0;
  padding: 0;
  letter-spacing: -0.466667px;
}

.eurotour-countries__list {
  @include list-reset;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.eurotour-countries__item {
  margin-right: 60px;
  margin-top: 101px;

  &:nth-last-child(2) {
    margin-right: 120px;
    margin-left: 70px;
  }

  &:nth-child(3n+2) {
    margin-right: 60px;
    margin-left: 0;
  }

  &:nth-child(3n) {
    margin-right: 0;
    margin-left: 0;
  }

  &:nth-child(-n+3) {
    margin-top: 0;
  }
}

@media(max-width: $desktop-width) {
  .eurotour-countries__header {
    padding: 0 percent-width(726px, 192px) 0 percent-width(726px, 191px);
    margin-bottom: 40px;
  }

  .eurotour-countries__headline {
    margin-bottom: 21px;
    letter-spacing: normal;
  }

  .eurotour-countries__description {
    text-align: center;
  }

  .eurotour-countries__list {
    justify-content: center;
  }

  .eurotour-countries__item {
    &:nth-child(n) {
      margin: 0;
      margin-right: percent-width(726px, 30px);
      margin-top: percent-width(726px, 20px);
    }

    &:last-child {
      margin-top: percent-width(726px, 30px);
      margin-left: percent-width(726px, 30px);
    }

    &:nth-child(2n) {
      margin-top: percent-width(726px, 20px);
      margin-right: 0;
    }

    &:nth-child(-n+2) {
      margin-top: 0;
    }
  }
}

@media(max-width: $tablet-width) {
  .eurotour-countries {
    color: $lightest-text-color
  }

  .eurotour-countries__header {
    padding: 0;
    margin-bottom: 19px;
  }

  .eurotour-countries__list {
    flex-wrap: nowrap;
    flex-direction: column;
  }

  .eurotour-countries__item {
    &:nth-child(n) {
      margin: 0 auto;
      margin-bottom: 35px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
