.price {
  display: flex;
  width: 1180px;
  padding-left: 5px;
 @include Montserrat;
 @include font(16px, 27px);
 color: $lightest-text-color;
}

.price__header {
  padding-top: 111px;
  order: 1;
}

.price__headline {
  margin-bottom: 48px;
  line-height: 90px;
}

.price__description {
  margin: 0;
  margin-bottom: 61px;
  padding: 0;
}

.price__connection {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;
  padding-left: 46px;
  @include font(22px, 34px);
  color: $dark-text-color;
  font-weight: 500;
}

.price__telephone {
  position: relative;
  display: block;
  padding-left: 46px;
  @include font(20px, 32px);
  color: black;
  text-decoration: none;
}

.price__telephone::before {
  content: "";

  position: absolute;
  left: -20px;
  display: block;
  width: 30px;
  height: 30px;
  background-image: url('../img/phone.svg');
}

.price__list {
  display: flex;
  @include list-reset;
  margin-right: 109px;
}

.price__item {
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }
}

@media(max-width: $desktop-width) {
  .price {
    flex-direction: column;
    width: percent-width(768px, 726px);
    padding: 0;
  }

  .price__header {
    order: -1;
    margin-bottom: 29px;
    padding: 0;
  }

  .price__headline {
    margin-bottom: 20px;
    line-height: 46px;
  }

  .price__description {
    text-align: center;
    margin-bottom: 33px;
  }

  .price__connection,
  .price__telephone {
    font-size: 18px;
    padding: 0;
    text-align: center;
  }

  .price__telephone {
    color: $base-pink;
  }

  .price__telephone::before {
    left: 34%;
  }

  .price__list {
    margin: 0;
    justify-content: center;
  }

  .price__item {
    margin-right: 50px;
  }
}

@media(max-width: $tablet-width) {
  .price {
    width: percent-width(320px, 288px);
  }

  .price__headline {
    margin-bottom: 16px;
  }

  .price__description {
    margin-bottom: 22px;
  }

  .price__connection {
    margin-bottom: 10px;
  }

  .price__list {
    flex-direction: column;
  }

  .price__item {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .price__header {
    margin-bottom: 40px;
  }

  .price__telephone::before {
    left: calc(50% - 130px);
  }
}
