.page-header__wrapper {
  display: flex;
  align-items: center;
  padding: 33px 14px 33px 0;

  @include Montserrat;
}

.page-header__telephone {
  position: relative;
  @include font(18px, 32px);
  color: $base-pink;
  padding-top: 2px;
}

.page-header__telephone::before {
  content: "";

  position: absolute;
  left: -30px;
  display: block;
  width: 30px;
  height: 30px;
  background-image: url('../img/phone.svg');
}

.page-header__navigation {
  margin-left: auto;
  margin-right: 94px;
}

@media(max-width: $desktop-width) {
  .page-header {
    position: relative;
  }

  .page-header__image-link {
    display: block;
    padding: 18.5px 0 14.5px 0;
  }

  .page-header__wrapper {
    display: block;
    padding: 0;

    @include Montserrat;
  }

  .page-header__logo-image {
    width: 44px;
    height: 17px;
    display: block;
  }

  .page-header__telephone {
    display: none;
  }
}
