.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.center {
  width: 1170px;
  margin: 0 auto;
}

.headline {
  position: relative;
  margin: 0;
  padding: 0;
  @include font(55px, 80px);
  font-weight: 600;
  color: $middle-dark-text-color;
}

.headline::after {
  content: '';
  position: absolute;
  bottom: -19px;
  display: block;
  width: 175px;
  height: 3px;
  background-color: $base-pink;
}

.footer-headline {
  margin: 0;
  padding: 0;
  @include font(18px, 32px);
  font-weight: 600;
  color: $dark-text-color;
}

@media(max-width: $desktop-width) {
  .center {
    width: percent-width(768px, 726px);
  }

  .headline {
    @include font(30px, 45px);
    text-align: center;
  }

  .headline::after {
    display: none;
  }
}

@media(max-width: $tablet-width) {
  .center {
    width: percent-width(320px, 288px);
  }

  .headline {
    @include font(24px, 36px);
    text-align: center;
  }
}
