.success-popup {
  display: none;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 728px;
  min-height: 444px;

  @include Montserrat;
  @include font(25px, 43px);
  color: $popup-text-color;
  font-weight: 500;

  background-color: $popup-background;
}

.success-popup__close {
  position: absolute;
  top: 18px;
  right: 19px;
  display: block;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  background-image: url('../img/popup-close.svg');
}

.success-popup__description {
  width: 390px;
  text-align: center;
}
