.next-travels {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  width: 356px;
}

.next-travels::after {
  content: '';
  position: absolute;
  top: 0;
  left: 389px;
  width: 1px;
  height: 189px;
  background-color: $footer-separator-color;
  box-shadow: 0px 4px 4px $separator-shadow-color;
}

.next-travels__list {
  justify-content: space-between;
  flex-wrap: wrap;
  @include list-reset;
  margin-right: 25px;

  &:last-of-type {
    margin-right: 0;
  }
}

.next-travels__item {
  width: 102px;
  margin-top: 10px;
  @include font(14px, 17px);

  &:first-child {
    margin-top: 0;
  }

  // &:nth-child(-n+3) {
  //   margin-top: 0;
  // }

  // &:nth-child(3n+1) {
  //   width: 110px;
  // }
}

.next-travels__headline {
  margin-bottom: 21px;
  width: 100%;
}

@media(max-width: $desktop-width) {
  .next-travels {
    width: percent-width(726px, 376px);
  }

  .next-travels::after {
    box-shadow: none;
    left: percent-width(376px, 395px);
    height: 177px;
  }

  .next-travels__list {
    justify-content: flex-start;
    margin-right: percent-width(376px, 23px);
  }

}

@media(max-width: $tablet-width) {
  .next-travels {
    width: 100%;
    padding-left: percent-width(288px, 15px);
    margin-bottom: 195px;
    justify-content: flex-start;
  }

  .next-travels__list {
    margin: 0;
    margin-top: 21px;
    width: percent-width(288px, 102px);
    margin-right: percent-width(288px, 60px);

    &:nth-of-type(-n+2) {
      margin-top: 0;
    }

    &:last-of-type {
      margin-left: percent-width(288px, 80px);
    }

    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }

  .next-travels__item {
    text-align: center;
    width: auto;
  }

  .next-travels::after {
    top: auto;
    left: 0;
    bottom: -165px;
    height: 1px;
    width: 100%;
  }
}
