.main-navigation__toggle {
  display: none;
}

.main-navigation__list {
  display: flex;
  flex-wrap: wrap;
  @include list-reset;
}

.main-navigation {
  max-width: 530px;
}

.main-navigation__link {
  text-decoration: none;
  color: $dark-text-color;
  @include font(14px, 17px);
  font-weight: 500;
}

.main-navigation__link:hover,
.main-navigation__link:focus {
  color: $base-pink;
}

.main-navigation__item {
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }
}

@media(max-width: $desktop-width) {
  .main-navigation {
    max-width: none;
    margin-right: 0;
    background-color: white;
  }

  .main-navigation__list {
    width: 100%;
    display: none;

    flex-direction: column;
    flex-wrap: nowrap;
  }

  .main-navigation__link {
    display: block;
    padding: 17px percent-width(726px, 16px);
    color: $popup-text-color;
    @include font(20px, 28px);
    font-weight: 600;
    background-image: url('../img/icon.svg');
    background-repeat: no-repeat;
    background-position: 95% center;
  }

  .main-navigation__toggle {
    position: absolute;
    z-index: 101;
    top: 0;
    right: 0;
    display: block;
    padding: 0;
    width: 9.37vw;
    height: 50px;
    border: 0 none;
    background-color: $base-pink;
    background-image: url('../img/Menu.svg'),
                      url('../img/popup-close.svg');
    background-repeat: no-repeat;
    background-position:  center, -1000px center;
    background-size: percent-width(72px, 30px) percent-width(50px, 30px), auto;
  }

  .main-navigation__item {
    background-color: $popup-background;
    margin-right: 0;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .main-navigation--no-js .main-navigation__list {
    display: flex;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .main-navigation--no-js .main-navigation__toggle {
  display: none;
  }



  .main-navigation--open .main-navigation__list{
    display: flex;
    padding-top: 134px;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background-color: white;
    z-index: 100;
  }

  .main-navigation--open .main-navigation__toggle {
    background-position: -1000px center, center;
    background-color: white;
    position: fixed;
  }
}

@media(max-width: $tablet-width) {
  .main-navigation__toggle {
    width: percent-width(320px, 50px);
    background-size: percent-width(50px, 30px) percent-width(50px, 30px);
  }
}
