.questions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 1194px;
  min-height: 455px;

  @include Montserrat;
  @include font(16px, 24px);
  color: $dark-text-color;

  background-image: url('../img/desktop/questions@1x.jpg');
  background-size: 844px 455px;
  background-repeat: no-repeat;
  background-position: 24px 0;
}

@media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    .questions {
      background-image: url('../img/desktop/questions@2x.jpg');
    }
  }

.questions__form {
  width: 656px;
  margin-left: auto;
  padding: 50px 49px 50px 50px;

  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 4px 30px $shadow-color;

}

.questions__hedline {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  @include font(25px, 32px);
  font-weight: 600;
  text-align: center;
}

.questions__description {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  text-align: center;
}

.questions__form-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 25px;
}

.questions__field {
  display: block;
  width: 269px;
  padding: 16px 15px 15px 15px;
  @include font(14px, 17px);
  border: 1px solid $grey-color;
}

.questions__field--invalid {
  outline: 2px solid $base-pink;
  outline-offset: -1px;
}

.questions__field--telephone {
  padding-left: 51px;
  position: relative;
}

.questions__field-wrapper {
  margin-right: 19px;
  position: relative;

  &:last-of-type {
    margin-right: 0;
  }
}

.questions__field-decoration {
  position: absolute;
  top: 5px;
  left: 15px;
  color: red;
  z-index: 1;
}

.questions__field-decoration--telephone-index {
  color: $dark-text-color;
  top: 15px;
  left: 25px;
}

.questions__field::placeholder {
  color: $dark-text-color;
}

.questions__button {
  margin: 0 auto;
  border: 0 none;
  padding: 18px 10px 17px 10px;
  padding-right: 10px;
  padding-left: 10px;
  min-width: 176px;
  cursor: pointer;
}

@media(max-width: $desktop-width) {
  .questions {
    width: auto;
    align-items: center;
    padding: percent-width(768px, 21px);
    background-image: url('../img/tablet/questions@1x.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    .questions {
      background-image: url('../img/tablet/questions@2x.jpg');
    }
  }

  .questions__form {
    padding: 49px 49px 52px 50px;
    width: percent-width(726px, 658px);
    margin: 0;
  }

  .questions__form-wrapper {
    margin-bottom: 20px;
    justify-content: center;
  }

  .questions__button {
    min-width: percent-width(559px, 250px);
  }
}

@media(max-width: $tablet-width) {
  .questions {
    justify-content: flex-start;
    min-height: auto;
    padding: 0;
    background-image: none;
    background-color: $popup-background;
  }

  .questions__form {
    width: 100%;
    padding: 50px 0 50px 0;
    width: percent-width(320px, 288px);
    box-shadow: none;
    background-color: transparent;
  }

  .questions__hedline {
    margin-bottom: 20px;
  }

  .questions__description {
    margin-bottom: 20px;
  }

  .questions__button {
    width: 100%;
  }

  .questions__field {
    width: 100%;
  }

  .questions__field-wrapper {
    margin-right: 0;
    margin-bottom: 20px;
    width: 100%;

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}
