.page-footer {
  display: flex;
  padding-left: 13px;
  padding-right: 8px;
  padding-bottom: 75px;
  @include Montserrat;
  @include font(14px, 24px);
  color: $light-text-color;
}

.page-footer__next-travels {
  margin-right: 123px;
}

.page-footer__contacts {
  margin-right: auto;
}

@media(max-width: $desktop-width) {
  .page-footer {
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;
    padding-bottom: 50px;
  }

  .page-footer__next-travels {
    margin-right: 0px;
  }
}
