.corporate-travels {
  width: 322px;
}

.corporate-travels__headline {
  margin-bottom: 21px;
}

.corporate-travels__description {
  padding: 0;
  margin: 0;
}

@media(max-width: $desktop-width) {
  .corporate-travels {
    position: relative;
    width: 100%;
    margin-top: 60px;
    text-align: center;
  }

  .corporate-travels::before {
    content: '';
    position: absolute;
    top: -29px;
    display: block;
    width: 100%;
    height: 1px;
    background-color: $footer-separator-color;
  }
}

@media (max-width: $tablet-width) {
  .corporate-travels {
    margin-top: 25px;
  }
}
