.contacts {
  position: relative;
  width: 280px;
}

.contacts::after {
  content: '';
  position: absolute;
  top: 0;
  left: 300px;
  width: 1px;
  height: 189px;
  background-color: $footer-separator-color;

}

.contacts__link {
  display: block;
  @include font(14px, 17px);
  color: $base-pink;
  margin-bottom: 19px;
  padding-left: 41px;
  word-wrap: break-word;
}

.contacts__headline {
  margin-bottom: 23px;
}

.contacts__link--telephone {
  @include font(16px, 32px);
  position: relative;
}

.contacts__link--telephone::before {
  content: '';
  display: block;
  position: absolute;
  left: -5px;
  width: 30px;
  height: 30px;
  background-image: url('../img/phone-light.svg');
}

.contacts__link--mail {
  display: none;
}

.contacts__link--mail-address {
  position: relative;
}

.contacts__link--mail-address::before {
  content: '';
  display: block;
  position: absolute;
  left: -5px;
  width: 26px;
  height: 20px;
  background-image: url('../img/mail.svg');
}

@media(max-width: $desktop-width) {
  .contacts {
    width: percent-width(726px, 280px);
    margin-right: 0;
  }
  .contacts::after {
    display: none;
  }

  .contacts__link--mail-address {
    display: none;
  }

  .contacts__link--mail {
    display: block;
  }

  .contacts__link--mail::before {
    content: '';
    display: block;
    position: absolute;
    left: -5px;
    width: 26px;
    height: 20px;
    background-image: url('../img/mail.svg');
  }
}

@media(max-width: $tablet-width) {
  .contacts {
    width: 100%;
    text-align: center;
  }

  .contacts__link {
    padding: 0;
    padding-left: percent-width(288px, 30px);
  }

  .contacts__link--mail::before,
  .contacts__link--telephone::before  {
    left: calc(50% - 130px);
  }
}
