.countries-information {
  position: relative;
  flex-wrap: wrap;
  @include Montserrat;
  @include font(16px, 28px);
  color: $lightest-text-color;
  z-index: 1;
}

.countries-information__slide-list {
  width: 100%;
  @include list-reset;
  overflow: hidden;
}

.countries-information__slide-item {
  position: relative;
  min-height: 596px;
  opacity: 0;
  display: flex;
  transform: translateX(200%);
  align-items: center;
  justify-content: space-between;
  transition: opacity 3s
}

.countries-information__slide-name {
  display: block;
  padding-right: 99px;
  padding-bottom: 15px;

  border-bottom: 1px solid $grey-color;

  @include font(20px, 24px);
  font-weight: 500;
  letter-spacing: -0.415px;
  color: $grey-color;
  cursor: pointer;

  &:last-of-type {
    padding-right: 0;
  }
}

.countries-information__control-list {
  display: flex;
  @include list-reset;
  margin-bottom: 80px;
}

.countries-information__slide-name--active {
  color: $base-pink;
}

.countries-information__slide-item--active {
  display: flex;
  transform: translateX(0);
  opacity: 1;
}

.countries-information__slide-headline {
  line-height: 90px;
  letter-spacing: -1.245px;
  margin-bottom: 51px;
}

.countries-information__wrapper {
  width: 400px;
  margin-top: -8px;
}

.countries-information__reviews {
  position: relative;
  z-index: 3;
  width: 382px;
  padding: 29px 30px;
  background-color: white;
  @include font(14px, 28px);
}

.countries-information__slide-description {
  margin: 0;
  margin-bottom: 51px;
  padding: 0;
  padding-right: 30px;
  letter-spacing: -0.466667px;
}

.countries-information__buy-link {
  width: 250px;
}

.countries-information__image-container {
  position: absolute;
  top: 0;
  left: 471px;
  margin: 0;
  display: block;
  z-index: 2;
}

.countries-information__review-headline {
  margin: 0;
  margin-bottom: 33px;
  padding: 0;
  @include font(30px, 37px);
  font-weight: 500;
  letter-spacing: -0.691667px;
  color: $middle-dark-text-color
}

.countries-information__review-text {
  margin: 0;
  margin-bottom: 33px;
  padding: 0;
  letter-spacing: -0.408333px;
}

.countries-information__user-account {
  display: flex;
  align-items: center;
  padding-left: 1px;

  font-family: Arial, Helvetica, sans-serif;
  @include font(16px, 24px);
  font-weight: 500;
  letter-spacing: -0.466667px;
  color: $middle-dark-text-color;
}

.countries-information__user-name {
  margin: 0;
  padding: 0;
}

.countries-information__user-avatar {
  display: block;
  border-radius: 50%;
  margin-right: 16px;
}

@media(max-width: $desktop-width) {
  .countries-information {
    width: auto;
  }

  .countries-information__wrapper {
    display: flex;
    flex-direction: column;
    width: auto;
    margin-bottom: 110px;
    min-height: 302px;
  }

  .countries-information__slider-container {
    overflow-x: hidden;
  }

  .countries-information__slide-list {
    display: flex;
    box-sizing: border-box;
    overflow: visible;
  }

  .countries-information__slide-item {
    display: flex;
    width: 100%;
    flex-shrink: 0;
    height: auto;
    transform: translateX(0);
    opacity: 1;
  }

  .countries-information__slide-description {
    margin-bottom: 0;
    padding: 0;
    text-align: center;
  }

  .countries-information__slide-name {
    padding-right: percent-width(731px, 99px);
    flex-shrink: 0;
    width: 215px;

    &:last-of-type {
      width: 100%;
    }
  }

  .countries-information__slide-item {
    flex-direction: column;
    padding-left: percent-width(768px, 21px);
    padding-right: percent-width(768px, 21px);
  }

  .countries-information__image-container {
    z-index: -1;
    overflow: hidden;
    top: 41.99%;
    left: 0;
    width: 100%;
    height: 415px;
  }

  .countries-information__image {
    max-width: none;
    width: 100%;
    height: auto;
  }

  .countries-information__control-list {
    margin-bottom: 41px;
    transition: transform 1s ease 0s;
  }

  .countries-information__slider-control-wrapper {
    padding-left: percent-width(768px, 37px);
  }

  .countries-information__slide-headline {
    margin-bottom: 20px;
    line-height: 45px;
    letter-spacing: normal;
  }

  .countries-information__buy-link {
    margin: 0 auto;
    margin-top: auto;
    width: percent-width(726px, 250px);
  }

  .countries-information__user-name {
    letter-spacing: -0.466667px;
  }
}

@media(max-width: $tablet-width) {
  .countries-information__slide-name {
    padding-right: percent-width(304.5px, 100px);
  }

  .countries-information__slide-item {
    padding: 0 percent-width(320px, 15px);
  }

  .countries-information__wrapper {
    min-height: 523px;
    margin-bottom: 100px;
  }

  .countries-information__control-list {
    margin-bottom: 28px;
  }

  .countries-information__slide-headline {
    margin-bottom: 16px;
  }

  .countries-information__buy-link {
    width: 100%;
  }

  .countries-information__reviews {
    width: 268px;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    min-height: 431px;
    text-align: center;
  }

  .countries-information__review-headline {
    @include font(20px, 24px);
    position: relative;
  }

  .countries-information__review-headline::after {
    content: "";
    position: absolute;
    bottom: -14px;
    left: 78px;
    display: block;
    width: 68px;
    height: 1px;
    background-color: $base-pink;
  }

  .countries-information__review-text {
    margin-bottom: 18px;
  }

  .countries-information__user-account {
    flex-direction: column;
  }

  .countries-information__user-avatar {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .countries-information__image-container {
    top: auto;
    bottom: 67px;
  }

  .countries-information__image {
    position: relative;
    bottom: 0;
  }
}
