.country-card__content-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 350px;
  min-height: 430px;
  @include font(24px, 29px);
  letter-spacing: -0.415px;
  font-weight: 500;
  text-decoration: none;
  color: $middle-dark-text-color;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    background-color: $image-background-color;
    z-index: -2;
  }
}

.country-card__country {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 215px;
  min-height: 61px;
  padding: 0;
  margin: 0;
  background-color: white;
}

.country-card__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

@media(max-width: $desktop-width) {
  .country-card__content-link {
    width: auto;
    min-height: 55.34vw;;
    overflow-y: hidden;
  }

  .country-card {
    width: percent-width(726px, 288px);
  }

  .country-card__image {
    // top: auto;
    // bottom: calc(50% - 27.6vw);
    width: 100%;
    height: 100%;
  }
}

@media(max-width: $tablet-width) {
  .country-card {
    width: 288px;
    box-sizing: border-box;
  }

  .country-card__content-link {
    min-height: 425px;
  }
}
