.page-main {
  background-image: url('../img/Group\ 29.svg');
  background-repeat: no-repeat;
  background-size: 1443.66px 1088.9px;
  background-position: 50% 72.4%;
}

.page-main__promo {
  margin-bottom: 199px;
}

.page-main__eurotour-countries {
  margin-bottom: 106px;
}

.page-main__countries-information {
  margin-bottom: 241px;
}

.page-main__price {
  margin-bottom: 136px;
}

@media(max-width: $desktop-width) {
  .page-main {
    background-image: none;
  }

  .page-main__promo {
    margin-bottom: 80px;
  }

  .page-main__eurotour-countries {
    margin-bottom: 96px;
  }

  .page-main__countries-information {
    margin-bottom: 80px;
  }

  .page-main__price {
    margin-bottom: 124px;
  }
}

@media(max-width: $tablet-width) {
  .page-main__promo {
    margin-bottom: 109px;
  }

  .page-main__eurotour-countries {
    margin-bottom: 40px;
  }

  .page-main__countries-information {
    margin-bottom: 40px;
  }

  .page-main__price {
    margin-bottom: 17px;
  }
}
