.button {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 30px 10px;
  @include font(12px, 15px);
  font-weight: bold;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  background-color: $base-pink;
}

.button:focus,
.button:hover {
  background-color: $active-button;
}

@media(max-width: $desktop-width) {
  .button {
    padding: 19px 10px;
    @include font(10px, 12px);
    font-weight: 500;
    letter-spacing: 2px;
  }

  .button:focus,
  .button:hover {
    background-color: $base-pink;
  }

  .button:active {
    background-color: $active-button;
  }
}

