.price-card {
  display: flex;
  flex-direction: column;
  width: 370px;
  padding: 40px 29px 30px 29px;
  padding-top: 40px;

  @include font(16px, 24px);
  color: $middle-dark-text-color;

  border: 1px solid $grey-color;
  border-radius: 6px;
  background-color: white;
}

.price-card__check-icon {
  position: absolute;
  left: -5px;
  top: 5px;
  width: 22px;
  height: 22px;
}

.price-card:hover {
  border: 1px solid $base-pink;
  box-shadow: inset 0 0 0 2px $base-pink;
}

.price-card:hover .price-card__check-icon {
  fill: red;
}

.price-card:hover ::after {
  background-color: $base-pink;
  opacity: 0.5;
}

.price-card__wrapper {
  position: relative;
  margin-bottom: 58px;
}

.price-card__wrapper::after {
  content: '';
  position: absolute;
  bottom: -27px;
  display: block;
  width: 100%;
  height: 1px;
  background-color: $grey-color;
}

.price-card__headline {
  display: flex;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  @include font(28px, 34px);
  font-weight: 600;
  letter-spacing: -1.27273px;
  color: $price-card-color;
  letter-spacing: 0.1px;
}

.price-card__headline--econom {
  letter-spacing: -1.27273px;
}

.price-card__price {
  margin-left: auto;
  @include font(25px, 34px);
  letter-spacing: normal;
}

.price-card__description {
  margin: 0;
  padding: 0;
  padding-right: 20px;
}

.price-card__list {
  @include list-reset;
  margin-bottom: 15px;
}

.price-card__item {
  position: relative;
  margin-bottom: 7px;
  padding-left: 30px;
  @include font(16px, 32px);
  color: $middle-dark-text-color;

  &:nth-child(n+2) {
    margin-bottom: 15px;
  }
}

.price-card--econom .price-card__item {
  margin-bottom: 15px;
}

.price-card__button {
  margin-top: auto;
}

@media(max-width: $desktop-width) {
  .price-card {
    position: relative;
    width: percent-width(726px, 288px);
    padding: 30px percent-width(726px, 20px) 54px percent-width(726px, 20px);
  }

  .price-card__headline {
    font-size: 24px;
    flex-direction: column;
    text-align: center;
    margin-bottom: 10px;
  }

  .price-card__wrapper {
    margin-bottom: 31px;
  }

  .price-card__wrapper::after {
    bottom: -10px;
  }

  .price-card__price {
    margin: 0;
    margin-top: 10px;
    font-weight: bold;
  }

  .price-card__description {
    text-align: center;
    padding: 0;
  }

  .price-card__item {
    margin-bottom: 5px;
    padding-left: 12.1958%;

    &:nth-child(n+2) {
      margin-bottom: 10px;
    }
  }

  .price-card--econom .price-card__item {
    margin-bottom: 10px;
  }

  .price-card__button {
    position: absolute;
    bottom: -1px;
    left: -1px;
    width: calc( 100% + 2px );
  }
}

@media(max-width: $tablet-width) {
  .price-card {
    width: 100%;
    padding-left: percent-width(288px, 20px);
    padding-right: percent-width(288px, 20px);
  }

  .price-card__headline {
    margin-bottom: 12px;
  }

  .price-card__wrapper {
    margin-bottom: 30px;
  }
}
