.promo {
  @include Montserrat;
  background-image: url('../img/desktop/promo@1x.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 40% 40%;

  color: white;
}

@media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    .promo {
      background-image: url('../img/desktop/promo@2x.jpg');
    }
  }

.promo__wrapper {
  min-height: 860px;
  padding: 184px 80px 0 700px;
  @include font(16px, 27px);
}

.promo__headline {
  color: white;
  margin-bottom: 29px;
  letter-spacing: -1.10667px;
}

.promo__headline::after {
  display: none;
}

.promo__description {
  margin: 0;
  margin-bottom: 34px;
  padding: 0;
  letter-spacing: -0.466667px;
}

.promo__link {
  width: 340px;
  min-height: 75px;
  justify-content: flex-start;
  padding-left: 41px;
  background-image: url('../img/arrow-right.svg');
  background-repeat: no-repeat;
  background-size: 14px 14px;
  background-position: 90% 50%;
}

@media(max-width: $desktop-width) {
  .promo {
    background-image: url('../img/tablet/promo@1x.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 40% 40%;

    color: white;
  }

  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    .promo {
      background-image: url('../img/tablet/promo@2x.jpg');
    }
  }

  .promo__wrapper {
    min-height: 344px;
    padding: 60px percent-width(768px, 164px) 0 percent-width(768px, 164px);
  }

  .promo__headline {
    letter-spacing: normal;
    margin-bottom: 15px;
  }

  .promo__description {
    margin-bottom: 10px;
    text-align: center;
  }

  .promo__link {
    width: 250px;
    min-height: 50px;
    margin: 0 auto;
  }
}

@media(max-width: $tablet-width) {
  .promo {
    position: relative;
    background-image: none;
    color: $middle-dark-text-color;
  }

  .promo__wrapper {
    padding: 81px 0 0 0;
  }

  .promo::after {
    content: '';
    position: absolute;
    top: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 235px;
    background-image: url('../img/mobile/promo@1x.jpg');
    background-repeat: no-repeat;
    background-size: cover;
  }

  @media(min-resolution: $retina-dppx), (min-resolution: $retina-dpi) {
    .promo::after {
      background-image: url('../img/mobile/promo@2x.jpg');
    }
  }

  .promo__headline {
    min-height: 154px;
    padding-left: 2%;
    margin-bottom: 21px;
  }

  .promo__description {
    margin-bottom: 20px;
  }

  .promo__link {
    width: 100%;
  }
}
