.buy-tour__popup {
  position: fixed;
  z-index: 15;

  top: -100%;
  left: calc(50% - 364px);
  width: 728px;
  min-height: 444px;
  padding-left: 397px;
  padding-right: 61px;
  padding-top: 71px;
  background-color: white;
  opacity: 0;

  @include Montserrat;
  @include font(14px, 17px);
  color: $popup-text-color;

  background-color: $popup-background;
  background-image: url('../img/desktop/popup@1x.jpg');
  background-repeat: no-repeat;
  transition: top 2s, opacity 3s;
}

.buy-tour__wrapper {
  position: fixed;
  z-index: 10;

  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: grey;
  opacity: 0;
  transition: opacity 3s;
}

.buy-tour__popup--active {
  top: 30%;
  opacity: 1;
}

.buy-tour__popup--active + .buy-tour__wrapper {
  top: 0;
  opacity: 0.7;
}

.buy-tour__headline {
  margin: 0;
  margin-bottom: 51px;
  padding: 0;
  @include font(25px, 43px);
  font-weight: 600;
  text-align: center;
}

.buy-tour__field {
  display: block;
  padding: 16px 15px 15px 15px;
  width: 100%;
  @include font(14px, 17px);
  border: 1px solid $popup-input-color;
}

.buy-tour__field-wrapper {
  margin-bottom: 21px;
  position: relative;

  &:last-of-type {
    margin-bottom: 20px;
  }
}

.buy-tour__field--telephone {
  padding-left: 51px;
}

.buy-tour__field::placeholder {
  color: $popup-text-color;
}

.buy-tour__close {
  position: absolute;
  top: 18px;
  right: 19px;
  display: block;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  background-image: url('../img/popup-close.svg');
  cursor: pointer;
}

.buy-tour__button {
  margin: 0 auto;
  min-width: 176px;
  min-height: 50px;
  border: 0 none;
  cursor: pointer;
}

.buy-tour__field-decoration {
  position: absolute;
  top: 5px;
  left: 15px;
  color: red;
  z-index: 1;
}

.buy-tour__field-decoration--telephone-index {
  color: $dark-text-color;
  top: 15px;
  left: 25px;
}

@media(max-width: $tablet-width) {
  .buy-tour__popup {
    background-image: none;
    padding: 100px percent-width(320px, 16px) 185px percent-width(320px, 16px);
    width: 100%;
    left: auto;
    min-height: auto;
  }

  .buy-tour__popup--active {
    top: 5vh;
  }
}
